import { Popup, useMap } from 'react-leaflet';
import { CloseCircleOutlined } from  '@ant-design/icons';

const SplashMapPopup = props => {

  const map = useMap();

  const onClose = () =>
    map.closePopup();

  return (
    <Popup {...props} >
      {props.children}

      <CloseCircleOutlined 
        className="leaflet-tetrastylon-close-button" 
        onClick={onClose} />
    </Popup>
  )

}

export default SplashMapPopup;