import { Button } from 'antd';
import { useQuery, gql } from '@apollo/client';
import toCSV from './toCSV';

const QUERY = gql`
  query {
    items {
      house(sort: "city", limit: 1000) {
        id,
        house_name,
        city,
        latitude,
        longitude,
        construction_date,
        construction_date_numeric,
        abandon_date,
        abandon_date_numeric,
        complete_plan,
        house_dimensions,
        cistern,
        images {
          id,
          directus_files_id {
            id
          }
        }
      }
    }
  }`

const Export = props => {

  const { loading, error, data } = useQuery(QUERY);

  const onDownloadCSV = () => {
    const csv = toCSV(data.items.house);

    const a = document.createElement('a');
    a.href = 'data:text/csv;charset=utf-8,' + encodeURI(csv);
    a.target = '_blank';
    a.download = 'houses.csv';
    a.click();
  }

  return (
    <div>
      <Button disabled={!data} onClick={onDownloadCSV}>Download</Button>
    </div>
  )

}

export default Export;