import { useState } from 'react';
import { EyeOutlined } from '@ant-design/icons';
import Lightbox from 'react-image-lightbox';

import './ImageGroup.scss';
import 'react-image-lightbox/style.css';

const ImageGroup = props => {

  const { images } = props;

  const [ selected, setSelected ] = useState(null);

  const { title, description } = selected !== null ? images[selected].directus_files_id : {};
  
  const getURL = img =>
    `${process.env.REACT_APP_API_URL}/assets/${img.directus_files_id.id}`

  const getPrevURL = idx => {
    if (selected !== null && images.length > 1) {
      const prevIdx = (images.length + selected - 1) % images.length;
      return getURL(images[prevIdx])
    }
  }

  const getNextURL = idx => {
    if (selected !== null && images.length > 1) {
      const nextIdx = (selected + 1) % images.length;
      return getURL(images[nextIdx]);
    }
  }

  return (
    <div className="image-group">
      {images.map((img, idx) => 
        <div
          key={img.id}
          className="image-wrapper"
          onClick={() => setSelected(idx)}>
          
          <img 
            src={`${getURL(img)}?width=200&height=200`} />

          <div className="dim-mask">
            <label><EyeOutlined /> Preview</label>
          </div>
        </div>
      )}

      {selected !== null && 
        <Lightbox 
          mainSrc={`${getURL(images[selected])}?width=1200`} 
          prevSrc={getPrevURL()}
          nextSrc={getNextURL()}
          imageCaption={
            <div className="image-caption">
              <h4>{title}</h4> 
              { description && <p>{ description }</p> }
            </div>
          }
          onMovePrevRequest={() => setSelected((images.length + selected - 1) % images.length)}
          onMoveNextRequest={() => setSelected((selected + 1) % images.length)}
          onCloseRequest={() => setSelected(null) } />
      }
    </div>
  )
  
}

export default ImageGroup;