import { useState } from 'react';
import { Switch, Tooltip } from 'antd';
import { MapContainer, TileLayer, Marker } from 'react-leaflet';
import ReactMarkdown from 'react-markdown';
import copy from 'copy-html-to-clipboard';
import ImageGroup from './ImageGroup';
import Navbar from '../Navbar';
import { 
  BankOutlined,
  CheckSquareOutlined,
  CloseSquareOutlined, 
  CopyOutlined, 
  HourglassOutlined 
} from '@ant-design/icons';

import './House.scss';

const BASEMAPS = {
  street: 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}',
  aerial: 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}'
}

const buildCitation = props => {
  
  const port = window.location.port ? `:${window.location.port}` : '';

  const permalink = `${window.location.protocol}//${window.location.hostname}${port}/house/${props.id}`;

  return (
    <span>
      { props.user_created.last_name }, { props.user_created.first_name.substring(0, 1) }
      ., "{ props.house_name }", Tetrastylon, {new Date(props.date_updated).getUTCFullYear()}
      , <a href={permalink}>{permalink}</a>.
    </span>
  )
}

const copyToClipboard = () => {
  const el = document.querySelector('.section.cite-as p');

  copy(el.innerHTML, {
    asHtml: true,
  });
}

const HouseLoaded = props => {

  const [ basemap, setBasemap ] = useState(BASEMAPS.street);

  const imageIds = props.images ? props.images.map(img => img.directus_files_id) : [];

  const backgroundImage = imageIds.length > 0 ? `url('${process.env.REACT_APP_API_URL}/assets/${imageIds[0].id}?width=1400&height=600')` : null; 

  const location = [ props.latitude, props.longitude ];

  const onToggleBasemap = useAerial => useAerial ? 
    setBasemap(BASEMAPS.aerial) : setBasemap(BASEMAPS.street);

  return (
    <div className="house loaded">
      <header>
        <Navbar />

        <div className="jumbo" style={{ backgroundImage }}>
          <div className="jumbo-contents">
            <h1>{ props.house_name }</h1>          
            <h2>{ props.city }</h2>
          </div>

          <div>
            <div className="mini-map">
              <MapContainer center={location} zoom={5} zoomControl={false} attributionControl={false}>
                {/* Forces a redraw. Kind of an odd hack to account for the fact that TileLayer is immutable */}
                { basemap == BASEMAPS.street && <TileLayer url={basemap} /> }
                { basemap == BASEMAPS.aerial && <TileLayer url={basemap} /> }
                <Marker position={location} />
              </MapContainer>
              
              <div className="layer-toggle">
                <Switch 
                  onChange={onToggleBasemap} 
                  checkedChildren="SATELLITE"
                  unCheckedChildren="STREET" />
              </div>
            </div>  
          </div>
        </div>
      </header>

      <main>
        <div className="section key-properties">
          <ul>
            <li>
              <BankOutlined /> { props.number_of_rooms } Rooms, { parseFloat(props.house_dimensions) } m²
            </li>
            <li>
              { props.building_phases && <>
                <HourglassOutlined /> { props.building_phases } Building Phase{props.building_phases > 0 && 's'}
              </>}
            </li>
            <li>
              <label>Constructed</label> <span className="nowrap">{props.construction_date }</span>
            </li>
            <li>
              <label>Abandoned</label> <span className="nowrap">{ props.abandon_date }</span>
            </li>
          </ul>
        </div>

        <div className="section">
          <h3>Dating of Phases</h3>
          <ReactMarkdown>{ props.dating_of_phases }</ReactMarkdown>
        </div>

        <div className="section boolean">
          <h3>Complete Plan</h3>
          {props.complete_plan ?
            <p>
              <CheckSquareOutlined /> Complete plan available
            </p> : 
            
            <p>
              <CloseSquareOutlined /> No complete plan available
            </p>
          }
        </div>

        <div className="section">
          <h3>Situation of Entrance</h3>
          <ReactMarkdown>{ props.situation_of_entrance }</ReactMarkdown>
        </div>
        
        <div className="section">
          <h3>Representation Room Typology</h3>
          <ReactMarkdown>{ props.representation_room_typology }</ReactMarkdown>
        </div>

        <div className="section atrium">
          <h3>Atrium</h3>
          <ReactMarkdown>{ props.atrium_typology }</ReactMarkdown>
          { props.atrium_typology && props.atrium_extension ? ', ' : ''} 
          { props.atrium_extension && `${parseFloat(props.atrium_extension)} m²` }
          { !(props.atrium_extension || props.atrium_typology) && '-' }
        </div>

        <div className="section">
          <h3>Impluvium Extension</h3>
          { props.impluvium_extension ? `${parseFloat(props.impluvium_extension)} m²` : '-' }
        </div>

        <div className="section boolean">
          <h3>Cistern</h3>
          {props.cistern ?
            <p>
              <CheckSquareOutlined /> This house has a cistern
            </p> : 
            
            <p>
              <CloseSquareOutlined /> No cistern
            </p>
          }
        </div>

        <div className="section">
          <h3>Other Uncovered Spaces</h3>
          <ReactMarkdown>{ props.other_uncovered_spaces }</ReactMarkdown>
        </div>

        { props.comments && 
          <div className="section">
            <h3>Comments</h3>
            <ReactMarkdown>{ props.comments }</ReactMarkdown>
          </div>
        }

        <div className="section description">
          <h3>Description</h3>
          <ReactMarkdown>{ props.house_description }</ReactMarkdown>  
        </div>

        { props.images && props.images.length > 0 &&
          <div className="section images">
            <h3>Images</h3>
            <ImageGroup images={props.images} />
          </div>
        }

        { props.plans_of_the_house && props.plans_of_the_house.length > 0 &&
          <div className="section plans">
            <h3>Plans of the House</h3>
            <ImageGroup images={props.plans_of_the_house} />
          </div>
        }

        { props.interesting_links && props.interesting_links.length > 0 &&
          <div className="section links">
            <h3>Interesting Links</h3>
            <ul>
              { props.interesting_links.map(link => 
                <li key={link.id}>
                  <a href={link.url} target="_blank">{ link.description }</a>
                </li>
              )}
            </ul>
          </div>
        }

        <div className="section bibliography">
          <h3>Bibliography</h3>
          <ReactMarkdown>{ props.bibliography }</ReactMarkdown>
        </div>

        <div className="section cite-as" >
          <h3>Cite as</h3>
          <p>
            { buildCitation(props) }
            <Tooltip title="Copy to Clipboard">
              <CopyOutlined className="copy" onClick={copyToClipboard} />
            </Tooltip>
          </p>
        </div>
      </main>
    </div>
  )

}

export default HouseLoaded;
