import { useState } from 'react';
import { useQuery, gql } from '@apollo/client';
import { Input } from 'antd';
import Navbar from '../Navbar';
import SearchResults from './SearchResults';
import SearchResultsError from './SearchResultsError';
import { checkIsLoggedIn } from '../loggedIn';

const buildQuery = (query, sorting) => { 

  // Sort by city, alphabetic, by default
  const sort = sorting ?? { field: 'city', order: 'ascend' };

  const conditions = [];

  if (query)
    conditions.push(`search: "${query}"`);

  if (sort.order === 'ascend')
    conditions.push(`sort: "${sort.field}"`);

  if (sort.order === 'descend')
    conditions.push(`sort: "-${sort.field}"`);

  conditions.push('limit: 1000');

  return gql`
    query {
      items {
        house(${conditions.join(', ')}) {
          id,
          house_name,
          city,
          latitude,
          longitude,
          construction_date,
          construction_date_numeric,
          abandon_date,
          abandon_date_numeric,
          complete_plan,
          house_dimensions,
          cistern,
          images {
            id,
            directus_files_id {
              id
            }
          }
        }
      }
    }`
}

const Search = props => {

  const [ query, setQuery ] = useState();

  const [ sorting, setSorting ] = useState();

  checkIsLoggedIn();

  return (
    <div className="search">
      <header>
        <Navbar />
      </header>
      
      <main>
        <div className="search-bar">
          <Input.Search 
            placeholder="Search..." 
            allowClear 
            size="large"
            onSearch={value => setQuery(value)} />
        </div>
        
        <div className="search-results">
          <SearchResultsContainer 
            query={query} 
            sorting={sorting} 
            onChangeSorting={sorting => setSorting(sorting)} /> 
        </div>
      </main>
    </div>
  )

}

function SearchResultsContainer(props) {

  const { loading, error, data } = useQuery(buildQuery(props.query, props.sorting));

  

  if (loading)
    return <SearchResults loading={true} />
  else if (error)
    return <SearchResultsError />
  else 
    return <SearchResults data={data.items.house} onChangeSorting={props.onChangeSorting} />

}

export default Search;
