import { Switch } from 'antd';
import { useState } from 'react';
import { MapContainer, TileLayer, Marker, useMap } from 'react-leaflet';
import SplashMapPopup from './SplashMapPopup';
import { useQuery, gql } from '@apollo/client';

const BASEMAPS = {
  street: 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Street_Map/MapServer/tile/{z}/{y}/{x}',
  aerial: 'https://server.arcgisonline.com/ArcGIS/rest/services/World_Imagery/MapServer/tile/{z}/{y}/{x}'
}

const QUERY = gql`
  query {
    items {
      house {
        id,
        house_name,
        city,
        latitude,
        longitude,
        images(limit: 1) {
          id,
          directus_files_id {
            id
          }
        }
      }
    }
  }`;

const getBounds = houses => {
  const lats = houses.map(h => h.latitude);
  const lons = houses.map(h => h.longitude);

  // Leaflet format
  return [
    [ Math.min(...lats), Math.min(...lons) ],
    [ Math.max(...lats), Math.max(...lons) ]
  ];
}

const SplashMap = props => {

  const [ basemap, setBasemap ] = useState(BASEMAPS.street);

  const { loading, error, data } = useQuery(QUERY);

  const onToggleBasemap = useAerial => useAerial ? 
    setBasemap(BASEMAPS.aerial) : setBasemap(BASEMAPS.street);

  const markers = data ? data.items.house.map(house => 
    <Marker key={house.id} position={[ house.latitude, house.longitude ]}>
      <SplashMapPopup>
        { house.images?.length > 0 && 
          <div className="popup-thumbnail">
            <img src={`${process.env.REACT_APP_API_URL}/assets/${house.images[0].directus_files_id.id}?width=240&height=240`} />
          </div>
        }

        <div className="popup-body">
          <h1>{ house.house_name }</h1>
          <h2>{ house.city }</h2>
          <a href={`/house/${house.id}`}>Details</a>
        </div>
      </SplashMapPopup>
    </Marker>
  ) : [];

  const location = [ 40, 15 ];

  return (
    <>
      { data && 
        <MapContainer 
          zoomControl={false} 
          attributionControl={false} 
          bounds={data ? getBounds(data.items.house) : null}
          boundsOptions={{ padding: [ 200, 200 ] }}>
          
          {/* Forces a redraw. Kind of an odd hack to account for the fact that TileLayer is immutable */}
          { basemap == BASEMAPS.street && <TileLayer url={basemap} /> }
          { basemap == BASEMAPS.aerial && <TileLayer url={basemap} /> }
          { markers }
        </MapContainer>
      }

      <div className="layer-toggle">
        <Switch 
          onChange={onToggleBasemap} 
          checkedChildren="SATELLITE"
          unCheckedChildren="STREET" />
      </div>
    </>
  )

}

export default SplashMap;
